  border-radius: 15% 15% 0% 0% / 3.3em 3.3em 0em 0em;
}

.mobile-tab-bottom{
  border-radius: 0% 0% 15% 15% / 0em 0em 3.3em 3.3em;
}

.mobile-tab-selected-tab-container{
  background-color: rgba(235, 235, 235, 0.95);
  border: 3px solid #000;
  border-top: none;
  margin: 0.25em 0.5em;
