  background-color: inherit;
}

.mobile-submit-button:active {
  background-color: #00f;
}

.mobile-close-button {
  border: 1px solid #000;
  background-color: #f00;
  border-radius: 0.3em;
  margin: 1%;
}

.mobile-dropdown{
  border: 1px solid #000;
